.products {
  margin-top: 91px;
}

@media screen and (min-width: 1024px) {
  .products {
    margin-top: 68px;
    display: flex;
  }
}

@media screen and (min-width: 1920px) {
  .products {
    margin-top: 52px;
  }
}

.products__list {
  display: grid;
  grid-template-columns: repeat(1, minmax(0, 1fr));
  justify-items: center;
  row-gap: 30px;
}

.products__list-empty {
  margin: 0;
  padding-top: 40px;
  font-family: 'Poppins', Arial, Helvetica, sans-serif;
  font-size: 26px;
  color: gray;
  text-align: center;
}

@media screen and (min-width: 1024px) {
  .products__list {
    border-radius: 8px;
    box-shadow: 0px 1px 2px rgba(58, 58, 68, 0.24),
      0px 2px 4px rgba(90, 91, 106, 0.24);
    grid-template-columns: repeat(2, minmax(0, 1fr));
    column-gap: 64px;
    row-gap: 32px;
    padding: 14px 58px;
  }
}

@media screen and (min-width: 1280px) {
  .products__list {
    grid-template-columns: repeat(3, minmax(0, 1fr));
    column-gap: 16px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

@media screen and (min-width: 1600px) {
  .products__list {
    grid-template-columns: repeat(4, minmax(0, 1fr));
    row-gap: 16px;
    padding-top: 21px;
    padding-bottom: 21px;
  }
}

.products__link {
  display: none;
}

@media screen and (min-width: 1024px) {
  .products__link {
    display: flex;
    flex-direction: column;
    padding-left: 30px;
    padding-right: 52px;
  }
}

@media screen and (min-width: 1280px) {
  .products__link {
    padding-right: 140px;
  }
}

@media screen and (min-width: 1600px) {
  .products__link {
    padding-top: 8px;
    padding-left: 132px;
    padding-right: 273px;
  }
}
